export class Julia {

    /**
     * @param {float} cr C
     * @param {float} ci C
     */
    constructor(cr, ci) {
        this._self = {};
        this._self.lastMaxNumber = null;
        this._self.lastMaxNumberReal = 0;
        this._self.lastMaxNumberImag = 0;
        this.cr = parseFloat(cr);
        this.ci = parseFloat(ci);
    }

    set limit(limit) {
        this._self.limit = parseFloat(limit);
    }

    get limit() {
        return parseInt(this._self.limit);
    }

    set loopMax(loopMax) {
        this._self.loopMax = parseInt(loopMax);
    }

    get loopMax() {
        return parseInt(this._self.loopMax);
    }

    /**
     * @param {float} real 
     * @param {float} imag 
     * @return {int} 
     *
     * 
     */
    countValue(real, imag) {
        /* riC */
        let r = this.cr;
        let i = this.ci;
        let zr = parseFloat(real), zi = parseFloat(imag), zr_next = 0, zi_next = 0;
        let c = 0;
        let modules = null;
        while (c < this._self.loopMax) {
            /* Z */
            zr_next = zr * zr - zi * zi;
            zi_next = 2 * zr * zi;
            /* ZCZ */
            zr = zr_next + r;
            zi = zi_next + i;
            c++;
            /*  */
            modules = Math.sqrt(zr * zr + zi * zi);
            if (modules >= this._self.limit) {
                return this._self.limit;
            }
        }
        return modules;
    }

    /**
     * @param {float} real 
     * @param {float} imag 
     * @return {int|bool} 
     *
     * 
     * loopMax0false
     */
    stopValue(real, imag) {
        /* riC */
        let r = this.cr;
        let i = this.ci;
        let zr = parseFloat(real), zi = parseFloat(imag), zr_next = 0, zi_next = 0;
        let c = 0;
        let modules = null;
        while (c < this._self.loopMax) {
            /* Z */
            zr_next = zr * zr - zi * zi;
            zi_next = 2 * zr * zi;
            /* ZCZ */
            zr = zr_next + r;
            zi = zi_next + i;
            c++;
            /*  */
            modules = Math.sqrt(zr * zr + zi * zi);
            if (modules >= this._self.limit) {
                return c;
            }
        }
        return false;
    }
}
